import React, { useState } from 'react'

export default function Block(props) {
    const state = props.state.level


    ////########VALIDATION##########///

    const [inBlock, setInBlocl] = useState([]);

    const heandleClick = (id, className) => {
        const arrBlock = inBlock

        const pushInBlock = {
            id,
            className
        }
        arrBlock.push(pushInBlock);

        setInBlocl(arrBlock)

        if (inBlock.length === 2) {
            if (inBlock[0].id !== inBlock[1].id) {
                if (inBlock[0].className === inBlock[1].className) {
                    const index_1 = state.findIndex(item => item.id === inBlock[0].id)
                    const index_2 = state.findIndex(item => item.id === inBlock[1].id)

                    props.comp(index_1, index_2)
                    setInBlocl([])


                    //####SETLEVEL#####///

                    if (state.filter(item => item.className === 'yellow').length === 16) {
                        if (state[0].lvl === 2) {
                            alert('win')
                            props.relodLvl();
                        }
                        else {
                            alert('Next level')
                            props.setLvl();
                        }
                    }
                    
                    //################//

                }
                else {
                    setInBlocl([])
                }
            }
            else {
                setInBlocl([])
            }
        }

        else {
            
        }

    }

    return (
        state.map(item => <button
            key={item.id}
            className={item.className}
            disabled = {item.disabled}
            onClick={() => heandleClick(item.id, item.className)}
        >Кликни!</button>)
    )
}
