import LVL_1 from "../../../Level/LVL_1"
import LVL_2 from "../../../Level/LVL_2";


const initialState = LVL_1();

export const levelReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'COMPRASION':
            state[action.payload.id1].className = 'yellow'
            state[action.payload.id2].className = 'yellow'
            break;
        
        case 'LEVEL_NEXT':
            state = LVL_2();
            break;
        
        case 'LEVEL_SET_ZERO' : 
            state = LVL_1() 
            break;

            default:  return state


    }
    return state
}