

const LEVEL_NEXT = 'LEVEL_NEXT'
const LEVEL_SET_ZERO = 'LEVEL_SET_ZERO'
const COMPRASION = 'COMPRASION'

const setLevelNext = () => {
    return {
        type: LEVEL_NEXT,
    }
}

const setLevelZero = () => {
    return {
        type: LEVEL_SET_ZERO,
    }
}
const Comprasion = (id1,id2) => {
    return {
        type: COMPRASION,
        payload: {
            id1,
            id2
        }
    }
}


export { setLevelNext, setLevelZero, Comprasion }