
import Block from './block';
import { connect } from 'react-redux';
import { setLevelNext, setLevelZero, Comprasion } from '../store/action/action';



const mapStateToProps = (state) => {
    return {
        state
    }
};

const mapStateDispatch = dispatch => {
    return {
        comp: (id1,id2) => dispatch(Comprasion(id1,id2)),
        setLvl: () => dispatch(setLevelNext()),
        relodLvl: () => dispatch(setLevelZero()),
    }
}


export default connect(mapStateToProps, mapStateDispatch)(Block);
